.container-client {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    margin-bottom: 100px;
}

.content-below-header-client {
    width: 100vw;
    overflow-x: hidden;
    position: absolute;
    top: 10%;
}

.client-position {
    display: flex;
    gap: 20px
}

.client-position-Content {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}


