/* Estilo para a tabela */
.container-table-client {
    position: relative;
    display: flex;
    flex-direction: column;
    /* Alteração para melhor organização */
    width: 80vw;
    height: max-content;
    padding: 50px 10px 20px 10px;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: aliceblue;
    border: 1px solid #ccc;
    border-radius: 8px;
    /* text-align: center; */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;

}

/* Ajuste do posicionamento horizontal */
.container-client-search {
    position: absolute;
    top: 3%;
    left: 4%;
    width: 300px;

}



.search-icon {
    width: 25px;
    z-index: 1;
    position: absolute;
    top: 8px;
    left: 8px;
}


.container-client-search input {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
    padding: 10px 10px 10px 40px !important;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
}

.client-table {
    width: 95%;
    border-collapse: collapse;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 3%;
    /* Centraliza a tabela na tela */
}

.client-table th,
.client-table td {
    border: 1px solid #ddd;
    background-color: #fff;
    padding: 8px;
    text-align: left;
}

.client-table th {
    background-color: var(--color-primary);
    /* Cor de fundo do cabeçalho */
    font-weight: bold;
    color: var(--color-white);
    /* Cor do texto no cabeçalho */
}

.pagination-client {
    display: flex;
    justify-content: center;
    align-items: flex-end;


}

.prev-pagination, .next-pagination{
    background-color: var(--color-primary);
    height: 48px;
    width: 50px;
    transition: transform 0.3s ease;
}
.prev-pagination:hover, .next-pagination:hover{
  transform: scale(1.1);
}
.prev-pagination{
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
    
}

.next-pagination{
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}


.middle-pagination{
    display: flex;
    justify-content: center;
    align-items: baseline;
    background-color: var(--color-primary);
    width: max-content;
    height: 47px;
    width: 130px;
    border-left: 1px solid #ccc ;
    border-right: 1px solid #ccc ;

    text-align: center;
    color:#fff ;
    padding: 5px 30px 0px 30px    ;
}




@media screen and (max-width: 600px) {

    .content-below-header-client {
        width: 100vw;
        overflow-x: hidden;
        position: absolute;
        top: 20%;
    }

    .title-card {
        padding-top: 10px;
        width: 100%;
        text-align: center;
        font-size: 1rem;
    }


    .value-card {
        font-size: 5rem;
    }

    .client-position-Content {
        text-align: center;
    }

    .container-client-search {
        position: absolute;
        top: 3%;
        left: 3%;
        width: 200px;
    }



    /* Ajuste do posicionamento horizontal */
    .container-table-client {
        width: 95vw;
        padding: 10px 0px;
    }

    .client-table {
        margin-top: 50px;
        width: 90vw;

    }

    td:nth-child(2), th:nth-child(2) {
        display: none;
    }

    .pagination-client button {
        width: 100px;
    }

    .container-client {
        margin-bottom: 30px;
    }
}

@media screen and (min-width: 601px) and (max-width: 768px) {

    .content-below-header-client {
        width: 100vw;
        overflow-x: hidden;
        position: absolute;
        top: 5%;
    }

    .title-card {
        padding-top: 10px;
        width: 100%;
        text-align: center;
        font-size: 1rem;
    }


    .value-card {
        font-size: 5rem;
    }

    .client-position-Content {
        text-align: center;
    }

    .container-client-search {
        position: absolute;
        top: 3%;
        left: 3%;
        width: 200px;
    }


    /* Ajuste do posicionamento horizontal */
    .container-table-client {
        width: 95vw;
        padding: 10px 0px;
    }

    .client-table {
        margin-top: 50px;
        width: 90vw;

    }

    td:nth-child(2), th:nth-child(2) {
        display: none;
    }

    .pagination-client button {
        width: 100px;
    }

    .client-position {
        justify-content: center;
    }


    .client-position-Content {
        width: 30vw;
    }

    .pending-appointments-home {
        width: 30vw;
    }



}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .content-below-header-client {
        overflow-x: hidden;
        top: 10%;
    }

    .container-table-client {
        width: 100vw;
    }

    .container-client-search {
        width: 300px;
    }

    .container-client-search input {
        font-size: 26px;
    }

    .client-table th {
        font-size: 1.5rem;
    }

    .client-table tr {
        font-size: 1.3rem;
    }

    .pagination-client button {
        font-size: 1.5rem;
    }

    .client-position {
        justify-content: center;
    }

    .client-position-Content {
        width: 32vw;
    }

    .pending-appointments-home {
        width: 32vw;
    }
}

@media screen and (min-width: 1025px) {

    .content-below-header-client {
        top: 10%;
    }

    .client-position {
        justify-content: center;
    }

    .client-position-Content {
        width: 25vw;
        max-width: 300px;
    }

    .pending-appointments-home {
        width: 25vw;
    }
}