/* Base */
.footer {
    background-color: var(--color-primary);
    color: var(--color-white);
    padding: 40px 20px;
    width: 100vw;
    overflow-x: hidden;
}

.footer-content {
    display: grid;
    grid-template-areas:
        "address social contato"
        "addres social contato"
        "copy copy copy";
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}


.footer-section {
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    gap: 5px;
}

.contact-info {
    display: flex;
    align-items: center;
}

.contact-info img {
    width: 20px;
    margin-right: 10px;
}




.social, .contact {
    width: max-content;
    justify-content: center;
    text-align: center;
}

.social-icons {
    margin-top: 20px;
    display: flex;
    gap: 20px;
}


.logo-footer {
    width: 120px;
}


.footer-bottom {
    text-align: center;
    margin-top: 40px;
    font-size: 1.1rem;
    opacity: 0.7;
}

/* Responsividade para dispositivos móveis */

@media screen and (max-width: 600px) {
    .footer {
        padding: 20px 10px;
    }

    .footer-content {
        display: grid;
        justify-items: start;
        grid-template-areas:
            "address social "
            "contato contato"
            "copy copy";
        grid-template-columns: repeat(auto, minmax(300px, 1fr));
        /* Align items at the start (top) of each grid item */
    }


    .container-andress {
        grid-template-columns: 1fr;
    }

    .footer-section {
        gap: 5px;
    }


    .footer-content> :nth-child(3) {
        margin-top: 30px;
        transform: translateX(0%);
    }

    .social h3 {
        width: 100%;
        text-align: center;
    }

    .social-icons a img {
        width: 40px;
    }

    .footer-bottom p {
        font-size: 0.8rem;
    }

    .logo-footer {
        width: 60px;
        margin-bottom: 10px;
    }
}

/* Responsividade para tablets */

@media screen and (min-width: 601px) and (max-width: 768px) {
    .footer-content {
        display: grid;
        grid-template-areas:
            "address social"
            "contato contato"
            "copy copy";
        grid-template-columns: repeat(1, minmax(300px, 1fr));
        /* Align items at the start (top) of each grid item */
    }

    .container-contact {
        flex-direction: column-reverse;
        display: flex;
        gap: 5px;
    }


    .container-andress .footer-section {
        width: 200px;
    }

    .social-icons img {
        width: 48px;
    }

    .footer-content> :nth-child(3) {
        transform: translateX(100%);
    }

}



/* Responsividade para monitores pequenos */

@media screen and (min-width: 769px) and (max-width: 1024px) {
    .footer-content {
        display: grid;
        gap: 20px;
        align-items: flex-start;
        justify-items: center;
        grid-template-areas:
            "address social contato "
            "copy copy copy";
        grid-template-columns: repeat(auto, minmax(300px, 1fr));
        /* Align items at the start (top) of each grid item */
    }


    .container-contact {
        flex-direction: column-reverse;
        display: flex;
        gap: 5px;
    }

    .social-icons img {
        width: 48px;
    }

    .logo-footer {
        width: 80px;
        margin-bottom: 10px;
    }

}

/* Responsividade para monitores maiores */

@media screen and (min-width: 1025px) {
    .footer-content {
        display: grid;
        gap: 20px;
        grid-template-areas:
            "address social contato"
            "copy copy copy";
        grid-template-columns: repeat(auto, minmax(300px, 1fr));
        /* Align items at the start (top) of each grid item */
    }

    .footer-content {
        grid-template-columns: repeat(3, 1fr);
        width: 100vw;
        padding-left: 15%;
    }

    .container-andress .footer-section {
        width: 200px;
    }

    .logo-footer {
        width: 100px;
        margin-bottom: 10px;
    }

}