/* Cadastro.css */

/* Estilos gerais */

.container-logo-register {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    gap: 10px
}

.logo-register {
    width: 80px;
}

.container-register {
    min-width: 350px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.container-register h2:first-child {
    text-align: center;
}

.form-group {
    margin-bottom: 15px;
}

.form-group label:first-child {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}


input[type='text'],
input[type='email'],
input[type='tel'],
input[type='password'] {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ccc;
}

button {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    color: var(--color-white);
    background-color: var(--color-primary);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: var(--color-primary);
}

.error-message {
    color: #ff0033;
    /* Vermelho mais claro */
    font-size: 14px;
    margin-top: 8px;
    background-color: #ffe6e6;
    /* Fundo vermelho claro */
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ff0033;
    /* Borda vermelha */


}