@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@200;300;400;600;700;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

body {
    font-family: var(--font-roboto);
}

:root {
    --color-primary: #0056b3;
    --color-secondary: #377bb9;
    --color-white: #fff;
    --color-black: #000;
    --font-roboto: 'Roboto Condensed', sans-serif;
}

.mg-top-2 {
    margin-top: 2rem;
}
.mg-bottom-2 {
    margin-bottom: 2rem;
}

.flex {
    display: flex;
    align-items: center;
    gap: 10px;
}

/* Estilos básicos para os cards - customize conforme necessário */
.home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow-x: hidden;

    width: 100vw;

}

.header-container {
    position: relative;
}

.content-below-header {
    position: absolute;
    top: 15%;
    /* ou o valor desejado para ajustar abaixo do cabeçalho */
}


.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    /* fundo semitransparente */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
    backdrop-filter: blur(5px);
    /* Ofusca o conteúdo por trás */
}

.confirmation-modal {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
    z-index: 1000;
    /* Mantém o modal acima do overlay */
}

.confirmation-modal h3 {
    margin-bottom: 10px;
}

.confirmation-modal p {
    margin-bottom: 15px;
}

.confirmation-content {
    display: flex;
    justify-content: center;

}

.btn-confirm {
    background-color: green;
    color: white;
    border: none;
    width: 50%;
    padding: 8px 16px;
    margin: 0 5px;
    cursor: pointer;
    border-radius: 4px;
}

.btn-cancel {
    background-color: red;
    color: white;
    border: none;
    width: 50%;
    padding: 8px 16px;
    margin: 0 5px;
    cursor: pointer;
    border-radius: 4px;
    box-shadow: 0px 1px 2px #000;
    transition: transform 0.3s ease;
}

.btn-cancel:hover {
    transform: scale(1.2);
}