.professional-card {
    position: relative;
    background-color: #f5f5f5;
}

.professional-delete {
    position: absolute;
    right: 15px;
    background-color: #ff6347;
    top: 7px;
    color: var(--color-white);
    cursor: pointer;
    font-weight: bold;
    border-radius: 1000%;
    padding: 0px 5px;
    font-size: 1.2rem;
    transition: background-color 0.3s ease;
}

.professional-delete:hover {
    transform: scale(1.2);
}

.professional-delete.is-ausent {
    background-color: #f5f5f5;
    color: red;
}

.professional-card button {
    background-color: var(--color-primary);
    color: var(--color-white);
    border: none;
    width: 50%;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    /* Estilos para o botão 'Selecionar' */
}


.professional-card.is-ausent {
    background-color: rgb(0, 0, 0);
    opacity: 0.55;
    color: var(--color-white);

}

.professional-card.is-ausent button {
    box-shadow: 2px 2px 10px 1px var(--color-white);
}

.professional-card.is-ausent button:first-child {
    background-color: green;
    color: var(--color-white);
}


.horizontal-scroll {
    overflow-x: auto;
    white-space: nowrap;
    width: 100vw;


    /* Adiciona uma transição suave de cor */

}

.btn-professional {
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.btn-professional button:nth-child(1) {
    background-color: red;
}



/* Para navegadores Webkit (Chrome, Safari) */
.horizontal-scroll::-webkit-scrollbar {
    width: 12px;
}

.horizontal-scroll::-webkit-scrollbar-track {
    background: #f5f5f5;
    /* Cor de fundo da trilha */
}

.horizontal-scroll::-webkit-scrollbar-thumb {
    background-color: #c0c0c0;
    /* Cor da alça da barra de rolagem */
    border-radius: 6px;
    border: 3px solid #f5f5f5;
}

.horizontal-scroll:hover {
    border-bottom-color: var(--color-primary);
    /* Cor diferente ao passar o mouse */
}

/* Para navegadores Mozilla (Firefox) */
.horizontal-scroll {
    scrollbar-width: thin;
    scrollbar-color: #c0c0c0 #f5f5f5;
    /* Cor da alça e fundo da trilha */
}

.container-professionals {
    transform: translateY(5%);
}

.table-block-times {
    display: flex;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 40px;
    width: 100vw;
}


.table-block-times table {
    width: 50%;
    text-align: center;
    border-collapse: collapse;
    border-radius: 20px;
}


.table-block-times table thead {
    background: var(--color-primary);
    border: 1px solid var(--color-primary);

}



.table-block-times th {
    padding: 15px;
    color: #f5f5f5;
    font-size: 1.2rem;
}

.table-block-times td:nth-child(1) {
    width: 30%;
}

.table-block-times td:nth-child(2) {
    width: 20%;
}

.table-block-times td:nth-child(3) {
    width: 40%;
}
.table-block-times td:nth-child(4) {
    width: 10%;
}

.table-block-times td {
    font-size: 1.2rem;
    background-color: #fff;
}

.container-icons-table{
    display: flex;
    gap: 5px;
}
.container-icons-table img {
    width: 40px;
    transition: transform 0.3s ease;
}
.container-icons-table img:hover {
   transform: scale(1.2);
}


@media screen and (max-width: 768px) {
    .professional-img {
        width: 100px;
        height: 100px;
    }


    .container-professionals {
        margin-bottom: 20px;
    }

    .container-professionals {
        justify-content: center;
        align-items: center;
    }

    .content-below-header-professionals {
        position: absolute;
        top: 20%;
    }

    .footer-professionals {
        position: relative;


    }

    .btn-professional {
        flex-direction: row;
        gap: 5px;
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {

    .content-below-header-professionals {
        position: absolute;
        overflow-x: hidden;
        top: 25%;
    }

    .professional-img {
        width: 130px;
        height: 130px;
    }

    .professional-card h3 {
        font-size: 1.5rem;
    }

    .professional-card button {
        font-size: 1.5rem;
        width: 20vw;
    }

}

@media screen and (min-width: 1025px) {

    .btn-professional {
        flex-direction: row;
        gap: 5px;
    }



}