.content-below-header-profile {
    position: absolute;
    top: 15%;
    overflow-x: hidden;



}

.profile-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 300px;
    margin-bottom: 300px;
}

.profile-card {
    position: relative;
    border-radius: 10px;
    width: 450px;
    height: max-content;
}

.user-info {
    display: flex;
    align-items: center;
}


.user-details {
    display: flex;
    height: min-content;
    gap: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.user-photo {
    width: 80px;
    height: 80px;
    border-radius: 20%;
    position: absolute;
    z-index: 1;
    top: 10px;
    box-shadow: 0px 0px 5px 1px #fff;
    left: 10px;
    object-fit: cover;
}

.initial-circle {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: var(--color-primary);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
}

.initial-letter {
    font-size: 34px;
    color: var(--color-white);
    font-weight: bold;
}


.editing {
    background-color: var(--color-primary);
    padding: 20px;
    border-radius: 20px;
    color: #fff;
    display: grid;

    grid-template-areas:
        "nome celular"
        "endereco bairro"
        "cidadeuf complemento"
        "btn-save btn-cancel";

    gap: 15px;
    /* Espaçamento entre as células */
    width: 50vw;
}

.card-profile {
    width: 100%;
    border-radius: 20px;
    box-shadow: 0px 0px 5px 1px var(--color-primary);
}

.card-profile div:nth-child(1) {
    background-color: var(--color-primary);
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    top: 0;
    height: 100px;
    width: 100%;
}

.card-profile div:nth-child(2) {
    background-color: var(--color-white);
    height: 10px;
    width: 100%;
}

.card-profile div:nth-child(3) {
    position: relative;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: var(--color-secondary);
    height: 180px;
    width: 100%;
}

.code-card {
    position: absolute;
    bottom: 10px;
    left: 150px;
    width: 130px;
}

.logo-card {
    position: absolute;
    width: 100px;
    bottom: 10px;
    right: 10px;
}

.input-field {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 14px;
}


.form-profile-input img {
    width: 40px;
}

.displayname-card p {
    font-weight: bold;
    font-size: 1.8rem;
    top: 15px;
    color: #fff;
    left: 100px;
    position: absolute;
}

.phone-card p {
    font-weight: bold;
    font-size: 1.2rem;
    left: 100px;
    top: -60px;
    color: #fff;
    z-index: 1;
    position: absolute;
}

.email-card p {
    font-weight: bold;
    font-size: 1.2rem;
    left: 100px;
    top: 75px;
    color: #fff;
    z-index: 1;
    position: absolute;
}

.vip-card {
    position: absolute;
    top: 10px;
    right: 15px;
}

.bg-card-body span:nth-child(1) {
    font-weight: bold;
    font-size: 1.2rem;
    top: 5px;
    left: 5px;
    color: #fff;
    z-index: 1;
    position: absolute;
}

.bg-card-body span:nth-child(2) {
    font-weight: bold;
    font-size: 1.2rem;
    top: 30px;
    left: 5px;
    color: #fff;
    z-index: 1;
    position: absolute;
}

.bg-card-body span:nth-child(3) {
    font-weight: bold;
    font-size: 1.2rem;
    top: 55px;
    left: 5px;
    color: #fff;
    z-index: 1;
    position: absolute;
}

.bg-card-body span:nth-child(4) {
    font-weight: bold;
    font-size: 1.2rem;
    top: 80px;
    left: 5px;
    color: #fff;
    z-index: 1;
    position: absolute;
}

.bg-card-body span:nth-child(5) {
    font-weight: bold;
    font-size: 1rem;
    bottom: 0;
    left: 150px;
    color: #fff;
    z-index: 1;
    position: absolute;
}

.edit-card {
    position: absolute;
    left: 100px;
    width: 50%;
    transition: transform 0.3s ease;
}

.edit-card:hover {
    position: absolute;
    left: 100px;
    width: 50%;
    transform: scale(1.2);
}

.save-button {
    padding: 8px 16px;
    background-color: #0eaf36;
    color: var(--color-white);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.cancel-button {
    padding: 8px 16px;
    background-color: #c01d1d;
    color: var(--color-white);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}



.cancel-button:hover {
    background-color: #5e0b0b;
}



@media screen and (max-width: 600px) {


    .content-below-header-profile {
        position: absolute;
        top: 15%;
    }



    .initial-circle {
        width: 80px;
        height: 80px;
    }


    .profile-card {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 80vw;
        height: max-content;
    }

    .editing {
        width: 75vw;
    }

    .footer-profile {
        position: relative;
        margin-top: 2rem;
    }

}

@media screen and (min-width: 601px) and (max-width: 768px) {

    .content-below-header {
        height: 50vh;
    }

    .profile-container {
        position: absolute;
        top: 80%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .profile-card {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 80vw;
    }

    .editing {
        width: 75vw;
    }

    .footer-profile {
        position: relative;
        margin-top: 2rem;
    }

}


/* Tablets em orientação paisagem (geralmente acima de 768px de largura) */
@media screen and (min-width: 768px) and (max-width: 1024px) {
    .footer-profile {
        position: fixed;
        left: 0;
        bottom: 0;
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .footer-profile {
        position: fixed;
        bottom: 0;
    }

    .lineInfo {
        margin-bottom: 20px;
    }

    .lineInfo img {
        width: 64px;
    }

    .lineInfo p {
        font-size: 1.5rem;
    }

    .edit-button, .editing button {
        font-size: 1.5rem;
    }

    .editing {
        width: 30vw;
    }

    .editing h5 {
        font-size: 1.4rem;
    }

    .editing input {
        width: 100%;
        font-size: 1.6rem;
    }


}