.horizontal-scroll {
    overflow-x: auto;
    white-space: nowrap;
    width: 100vw;
    /* Adiciona uma transição suave de cor */

}

.today-current {
    font-size: 2.8rem;
    /* Outros estilos desejados */
}

/* Para navegadores Webkit (Chrome, Safari) */
.horizontal-scroll::-webkit-scrollbar {
    width: 12px;
}

.horizontal-scroll::-webkit-scrollbar-track {
    background: #f5f5f5;
    /* Cor de fundo da trilha */
}

.horizontal-scroll::-webkit-scrollbar-thumb {
    background-color: #c0c0c0;
    /* Cor da alça da barra de rolagem */
    border-radius: 6px;
    border: 3px solid #f5f5f5;
}

.horizontal-scroll:hover {
    border-bottom-color: #0088cc;
    /* Cor diferente ao passar o mouse */
}

/* Para navegadores Mozilla (Firefox) */
.horizontal-scroll {
    scrollbar-width: thin;
    scrollbar-color: #c0c0c0 #f5f5f5;
    /* Cor da alça e fundo da trilha */
}

.container-appointments {
    width: 100vw;
    display: flex;
    flex-direction: column;

}

.professionals-list {
    display: flex;
    /* Adapte conforme necessário para o layout desejado */
}


.professional-card {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 20px;
    margin: 20px;
    min-width: 250px;
    text-align: center;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    /* Outros estilos para o cartão profissional */
}

.professional-img {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 50%;
    pointer-events: none;
    margin-bottom: 10px;
    /* Estilos para a imagem do profissional */
}

.professional-name {
    font-size: 18px;
    margin-bottom: 10px;
    /* Estilos para o nome do profissional */
}


.container-verified img {
    position: absolute;
    top: 130px;
    right: 55px;
    width: 50px;
}

.select-button {
    transition: transform 0.3s ease;
    box-shadow: 0px 1px 4px 0.5px #000;
}

.select-button:hover {
    background-color: #090c69;
    transform: scale(1.1);
    /* Estilos ao passar o mouse sobre o botão 'Selecionar' */
}

.select-time {
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
    /* Estilos para a seção de seleção de horário */
}


.container-dates {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

}

.container-dates::-webkit-scrollbar {
    display: none;
    /* Oculta a barra de rolagem no Chrome/Safari */
}

.date-card {
    flex: 0 0 10%;
    width: calc(20% - 10px);
    /* 20% do espaço, menos a margem */
    margin: 0 5px;

}

.date-button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 1.5rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-align: center;
    box-shadow: 0px 1px 4px 0.5px #000;
    transition: background-color 0.3s ease;
}

.date-button:hover {
    background-color: #f5f5f5;
    color: #000;
    cursor: pointer;
}

.date-button input {
    display: none;
}


.date-button.selected {
    background-color: #3498db;
    color: #fff;
}

.date-number {
    font-size: 3rem;
    font-weight: bold;
}

.date-day {
    font-size: 12px;
    text-transform: uppercase;
    margin-top: 5px;
}

.container-times {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}


.time-buttons-container {
    height: max-content;
    width: 70vw;
    border-radius: 8px;
    padding: 10px;
    margin-bottom: 20px;
}

.time-buttons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.time-button {
    background-color: #3498db;
    color: #fff;
    font-size: 1.5rem;
    border: none;
    width: min-content;
    border: 2px dashed var(--color-white);
    padding: 8px 16px;
    border-radius: 1000px;
    cursor: pointer;
    box-shadow: 0px 1px 4px 0.5px #000;
    transition: transform 0.3s ease;
    /* Estilos para os botões de horário */
}

.time-button:hover {
    background-color: #090c69;
    color: #f5f5f5;
    transform: scale(1.1);
    /* Estilos ao passar o mouse sobre os botões de horário */
}

/* ESTILO PARA O RESUMO  */
.appointment-summary {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 8px;

    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    padding: 0px;
    text-align: center;
    max-width: 400px;
    margin: 0 auto;
}

.appointment-summary button {
    transition: transform 0.3s ease;
    background-color: #007bff;
}

.appointment-summary button:hover {
    background-color: #090c69;
}

.summary-header {
    background-color: #007bff;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    padding: 20px 0;
}

.summary-header h2 {
    margin-bottom: 0;
    padding: 0px 10px;

    font-size: 28px;
    color: #fff;
}

.summary-details {
    text-align: left;
    padding: 10px;
}

.summary-details p {
    font-size: 1.2rem;
    margin-top: 10px;
}

.appointment-summary button, .appointment-confirmation button {
    font-size: 1.2rem;
    font-weight: bold;
    letter-spacing: 1.5px;
}

.appointment-summary button:hover {

    background-color: #0056b3;
}


.appointment-confirmation {
    position: relative;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 23px;
    text-align: center;
    max-width: 400px;
    margin: 0 auto;
}



.appointment-confirmation button {
    margin-top: 20px;
    padding: 12px 24px;
    width: 50%;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.summary-header-confirmation {
    margin-bottom: 20px;
}

.appointment-confirmation button:hover {
    transform: scale(1.1);
}



.congratulation-container {
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.footer-appointments {
    position: fixed;
    bottom: 0px;
}


/* Media query para exibir 3 cards em telas menores, como dispositivos móveis */
@media screen and (max-width: 600px) {
    .date-card {
        flex: 0 0 30%;
        /* Exibe apenas 3 cards por vez */
    }

    .footer-appointments {
        margin-top: 20px;
        position: relative;
        bottom: 0px;
    }

    .container-verified img {
        position: absolute;
        top: 90px;
        right: 70px;
        width: 40px;
    }

}

