@media screen and (min-width: 768px) and (max-width: 1024px) {
    .container-appointments {
        display: flex;
        flex-direction: column;
        overflow-y: hidden;
    }

    .footer-appointments {
        position: fixed;
        bottom: 0px;
    }

    .time-button {
        width: 20vw;
        font-size: 2rem;
    }

    .appointment-summary button {
        font-size: 2rem;

    }

    .summary-header h2 {
        font-size: 2rem;
    }

    .summary-details p {
        font-size: 1.6rem;
    }

    .summary-Header h2 {
        font-size: 2rem;
    }

    .congratulation-container p {
        font-size: 1.5rem;
        margin-top: 20px;
    }

    .linkDom button {
        font-size: 1.8rem;
    }

    .container-verified img {
        position: absolute;
        top: 110px;
        right: 60px;
        width: 40px;
    }
}

