/* estilos para os cards totais */
.container-day-position {
    display: flex;
    flex-direction: column;
}

.container-day-position h1:first-child {

    text-align: center;
    font-family: var(--font-roboto);
    font-weight: 400;
    width: 100%;
    background-color: var(--color-primary);
    color: var(--color-white);
    border-top-right-radius: 50px;
    border-top-left-radius: 50px;
}

.content-day-position {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.pending-appointments-home, .pending-finalized-home {
    position: relative;
    width: 250px;
    text-align: center;
    border: 1px solid #ddd;
    padding-bottom: 50px;
    margin-bottom: 15px;
    position: relative;
    background-color: #f9f9f9;
    z-index: -1;
}

.title-card {
    position: absolute;
    font-size: 1.6rem;
    bottom: 0;
    border: 1px solid #ccc;
    box-shadow: 0px 1px 3px 1px #000;
    width: 100%;
    background-color: var(--color-primary);
    padding: 5px 0px;
    color: #f9f9f9;
    text-align: center;
}

.value-card {
    font-size: 5rem;
}

/* Estilos para tela menor que 600px de largura */
@media screen and (max-width: 600px) {

    .content-day-position {
        flex-wrap: nowrap;
        justify-content: center;
        width: 100vw;
    }




    .pending-appointments-home, .pending-finalized-home {
        padding: 0px;
        width: 40vw;
    }


    .client-position {
        width: 100vw;
    }

    .client-position-Content {
        width: 47vw;
    }



}

@media screen and (min-width: 601px) and (max-width: 765px) {

    .content-day-position {
        flex-wrap: nowrap;
        justify-content: center;
        width: 100vw;
    }


    .pending-appointments-home, .pending-finalized-home {
        padding: 0px;
        width: 40vw;
        max-width: 200px;
    }

    .pending-finalized-home {
        max-width: 200px;
    }


    .client-position {
        width: 100vw;
    }

    .client-position-Content {
        width: 47vw;
    }



}

@media screen and (min-width: 766px) and (max-width: 1024px) {
    .content-day-position {
        flex-wrap: nowrap;
        justify-content: center;
        width: 100vw;
    }

    .container-day-position {
        width: 100vw;
    }


    .pending-appointments-home, .pending-finalized-home {
        padding: 20px 0px 0px 0px;
        width: 32vw;
        max-width: 250px;
    }

    .client-position {
        width: 100vw;
    }

    .client-position-Content {
        width: 47vw;
    }

    .title-card {
        font-size: 2.2rem;
    }

    .value-card {
        font-size: 5rem;
    }

}

@media screen and (min-width: 1025px) {
    .pending-appointments-home, .pending-finalized-home {
        padding-bottom: 50px;
        width: 32vw;
        max-width: 250px;
    }
}