.header {
    position: fixed;
    top: 0;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--color-primary);
    color: var(--color-white);
    padding: 10px 20px;
    z-index: 9999;
}

.container {
    display: flex;
    flex-direction: column;
    width: 100vw;
}

.containerLogo {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}




.containerLogo .user-info {
    display: flex;
    align-items: center;
}

.logo-photo {
    width: 100px;
    margin-right: 10px;
}

.logo-name {
    font-size: 16px;
}


/* Estilos para o menu hamburger */

.menu-toggle {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.menu-btn {
    width: 30px;
    height: 20px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.btn-line {
    width: 100%;
    height: 3px;
    background-color: var(--color-white);
    transition: all 0.3s ease;
}



.open .btn-line:nth-child(1) {
    transform: translateY(7px) rotate(45deg);
}

.open .btn-line:nth-child(2) {
    opacity: 0;
}

.open .btn-line:nth-child(3) {
    transform: translateY(-7px) rotate(-45deg);
}

/* Estilos para o menu lateral */
.linkDom {
    color: var(--color-white);
    padding: 8px 0;
    cursor: pointer;
    text-decoration: none;
}

.menu {
    position: absolute;
    top: 50px;
    right: 0px;
    background-color: var(--color-primary);
    border-radius: 8px;
    padding: 10px;
    list-style: none;
    display: none;
    flex-direction: column;
}


.linkDom {
    transition: transform 0.3 ease;
}

.linkDom:hover {
    text-decoration: underline;

    transform: scale(1.1);
}



.menu.open {
    display: flex;
    flex-direction: column;
    margin-top: 2rem
}

@media screen and (max-width:600px) {
    .logo-photo {
        width: 60px;
        margin-right: 10px;
    }
}

@media screen and (min-width:601px) and (max-width: 768px) {
    .menu.open {
        margin-top: 1rem
    }

    .logo-photo {
        width: 120px;
        margin-right: 10px;
    }

    .logo-name {
        display: none;
    }


}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .menu.open {
        margin-top: 6%
    }

    .linkDom {
        font-size: 1.5rem;
    }

    .logo-photo {
        width: 80px;
        margin-right: 10px;
    }

    .logo-name {
        font-size: 1.5rem;
    }
}

@media screen and (min-width:1024px) {
    .header {
        max-height: 10vh;
    }

    .menu.open {
        margin-top: 1%;
    }

    .linkDom {
        font-size: 1.2rem;
    }

    .logo-photo {
        width: 80px;
        margin-right: 10px;
    }

    .logo-name {
        font-size: 1.2rem;
    }
}