.container-history {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* Ajustado para alinhar ao topo */
    align-items: center;
}

.container-dados {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.content-below-header-history {
    position: absolute;
    overflow-x: hidden;
    top: 15%;
    /* ou o valor desejado para ajustar abaixo do cabeçalho */
}

/* Estilos para a tabela de histórico de atendimentos */

.user-completed-appointments {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 95vw;
    margin-top: 20px;
    margin: 20px auto;
    background-color: var(--color-white);
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
}

.user-completed-appointments {
    width: 100vw;
    /* Ajustado para ocupar 95% da largura disponível */
    margin: 20px auto;
    background-color: var(--color-white);
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow-x: auto;
    /* Adicionado overflow-x para rolagem horizontal se necessário */
}


.user-completed-appointments h2 {
    font-size: 1.5em;
    margin-bottom: 20px;
    text-align: center;
}

.container-history-position {
    display: flex;
    flex-direction: column;

    gap: 20px;
    align-items: center;
    /* Ajustado para centralizar os elementos */
}



.content-history-position {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.container-canvas {
    background-color: var(--color-white);
    border-radius: 10px;
    width: 800px;
    height: 400px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);

}

.appointments-table {
    width: 100%;
    /* Ajustado para ocupar toda a largura disponível */
    border-collapse: collapse;
    table-layout: fixed;
    /* Define o layout da tabela como fixo para controlar larguras das colunas */
}

.appointments-table th,
.appointments-table td {
    border: 1px solid #ccc;
    padding: 12px;
    text-align: center;
    word-wrap: break-word;
    /* Adicionado para que o texto quebre em várias linhas */
    max-width: 150px;
    /* Limita a largura máxima das células */
    overflow: hidden;
    text-overflow: ellipsis;
    /* Adiciona reticências para indicar texto cortado */
}

.appointments-table {
    width: 100%;
    /* Ajustado para ocupar toda a largura disponível */
    border-collapse: collapse;
    table-layout: fixed;
    /* Define o layout da tabela como fixo para controlar larguras das colunas */
}


.appointments-table th {
    background-color: var(--color-primary);
    color: var(--color-white);
    font-weight: bold;
}

.appointment-row:nth-child(even) {
    background-color: #f9f9f9;
}

/* Ajuste do posicionamento horizontal */
.search-container-history {
    width: 200px;
    left: 10%;
    transform: translateX(0%);

}

.search-container-history input {
    width: 250px;
    margin-bottom: 10px;
    padding: 10px 10px 10px 40px !important;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
}

.pagination-history {
    display: flex;
    justify-content: center;
    width: 100vw;
    gap: 20px;
}

.pagination-history button {
    width: 100px;
    height: 40px;
}


/* Media query para ajustar o layout da tabela em telas menores */
@media screen and (max-width: 600px) {

    .user-completed-appointments {
        margin: 0px
    }

    /* Dentro do seu arquivo de estilo CSS */
    .search-container-history {
        width: 200px;
        left: 10%;
        transform: translateX(-10%);
    }

    /* Dentro do seu arquivo de estilo CSS */

    .chart-container {
        width: 100%;
        /* Ajusta a largura do contêiner para preencher a largura disponível */
        overflow-x: auto;
        /* Adiciona a barra de rolagem horizontal conforme necessário */
        margin-bottom: 20px;
        /* Ajuste opcional para o espaçamento inferior */
    }

    .container-canvas {
        /* Define uma largura máxima para o gráfico, ajuste conforme necessário */
        max-width: 100%;
    }

    .user-completed-appointments {
        padding: 20px 0px;
    }

    .appointments-table th,
    .appointments-table td {
        font-size: 14px;
        /* Reduz o tamanho da fonte */
        padding: 8px;
        /* Reduz o espaço interno das células */
    }

    .appointments-table th {
        font-size: 14px;
        /* Reduz o tamanho da fonte do cabeçalho */
    }

    .pagination-history {
        display: flex;
        justify-content: center;
        gap: 20px;
        transform: translateX(0%);

    }

    .pagination-history button {
        text-align: center;
        width: 140px;
    }

    .content-below-header-history {
        position: absolute;
        top: 15%;
        /* ou o valor desejado para ajustar abaixo do cabeçalho */
    }
}

