@media screen and (min-width: 768px) and (max-width: 1024px) {


    .user-completed-appointments, .container-history {
        overflow-x: hidden;
    }

    .panding-appointments {
        padding: 7%;
    }

    .pending-appointments h3 {
        width: 100%;
        text-align: center;
        font-size: 1.8rem;
    }

    .pending-appointments h1 {
        width: 100%;
        text-align: center;
        font-size: 5rem;
    }

    .user-completed-appointments h2 {
        font-size: 1.9rem;
    }

    .search-container-history {
        width: 330px;
    }

    .search-container-history input:first-child {
        font-size: 1.8rem;
    }

    .appointments-table th {
        font-size: 1.5rem;
    }

    .appointments-table tr {
        font-size: 1.4rem;
    }

    .pagination-history {
        margin-bottom: 20px;
    }

    .pagination-history button {
        font-size: 1.5rem;
        height: max-content;
        width: 15vw;
    }

    .chart-container {
        width: 65vw;
    }
}

