.dayPosition-Content {
    display: flex;
}

.footer-home {
    position: fixed;
    bottom: 0;
}

.containerPhoneNumber {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.phoneNumber-content {
    text-align: center;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: var(--color-primary);
    color: var(--color-white);
    max-width: 320px;
    width: 90%;
}

.phoneNumber-content h3 {
    font-size: 22px;
    margin-bottom: 20px;
}

.phoneNumber-content input[type="text"] {
    padding: 12px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 6px;
    width: 100%;
    margin-bottom: 15px;
}

.phoneNumber-content button {
    padding: 12px 24px;
    font-size: 16px;
    background-color: #18a848;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.phoneNumber-content button:hover {
    background-color: #1c853f;
}

.invalid-number-message {
    color: red;
    font-size: 14px;
    margin-top: 8px;

}








/* Estilos para tela menor que 600px de largura */
@media screen and (max-width: 768px) {
    .content-below-header {
        top: 5%;
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {

    .content-below-header {
        top: 15%;
    }

}