.container-services {
    background-color: var(--color-white);
}

.container-services h1:first-child {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100vw;
    background-color: var(--color-primary);
    color: var(--color-white);
    border-top-right-radius: 50px;
    border-top-left-radius: 50px;
}

/* Para navegadores Mozilla (Firefox) */
.horizontal-scroll {
    scrollbar-width: thin;
    scrollbar-color: #c0c0c0 #f5f5f5;
    /* Cor da alça e fundo da trilha */
}

.horizontal-scroll {
    overflow-x: auto;
    white-space: nowrap;
}

/* Para navegadores Webkit (Chrome, Safari) */
.horizontal-scroll::-webkit-scrollbar {
    width: 12px;
}

.horizontal-scroll::-webkit-scrollbar-track {
    background: #f5f5f5;
    /* Cor de fundo da trilha */
}

.horizontal-scroll::-webkit-scrollbar-thumb {
    background-color: #c0c0c0;
    /* Cor da alça da barra de rolagem */
    border-radius: 6px;
    border: 3px solid #f5f5f5;
}



.services {
    display: flex;
    width: 100vw;
    padding: 15px 5px;
    gap: 20px;
    list-style: none;
    display: inline-flex;
}

.service-card {
    background-color: whitesmoke;
    border-radius: 8px;
    padding: 20px;
    min-width: 250px;
    text-align: center;
    box-shadow: 0px 1px 5px 1px var(--color-primary);
    transition: transform 0.3s ease-in-out;
}

.service-card:hover {
    transform: translateY(5px);
    box-shadow: 0px 1px 5px 1px var(--color-black);
    background-color: var(--color-secondary);
}

.service-card img {
    width: 100px;
    /* object-fit: fil; */
    border-radius: 1000px;
    margin-bottom: 15px;
}

.service-card h2 {
    margin-bottom: 10px;
}

.service-value {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 24px;
    color: var(--color-primary);
    /* Amarelo claro */
    text-align: center;
    margin-top: 10px;
}

.service-cardButton button {
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    background-color: var(--color-primary);
    color: var(--color-white);
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.service-cardButton button:hover {
    background-color: var(--color-primary);
}


.elemento {
    position: relative;
    overflow: hidden;
}

.elemento::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: var(--color-white);
    animation: moverPonto 4s linear infinite;
}

@keyframes moverPonto {
    0% {
        top: -5px;
        left: -5px;
    }

    25% {
        top: -5px;
        left: calc(100% - 5px);
    }

    50% {
        top: calc(100% - 5px);
        left: calc(100% - 5px);
    }

    75% {
        top: calc(100% - 5px);
        left: -5px;
    }

    100% {
        top: -5px;
        left: -5px;
    }
}

@media screen and (max-width: 768px) {
    .services {
        padding: 10px 0px;
        width: 100vw;
    }

    .container-services {
        border-radius: 40px;
    }

    .title-appointments h1 {
        font-size: 1.5rem;
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {

    .container-services {
        margin-bottom: 2rem;
    }

    .container-services h1 {
        font-size: 2.2rem;
        padding: 10px 0px;
    }

    .services {
        padding: 10px 0px;
        width: 100vw;
        margin-bottom: 50px;
    }

    .container-services {
        border-radius: 40px;
    }

    .service-card img {
        width: 120px;
    }

    .service-card h2 {
        font-size: 1.8rem;
    }

    .service-card p {
        font-size: 1.6rem;
    }

    .elemento {
        font-size: 1.2rem;
    }

}

@media screen and (min-width: 1024px) {

    .container-services {
        margin-bottom: 2rem;
    }

    .container-services h1 {
        font-size: 2.2rem;
        padding: 10px 0px;
    }


    .container-services {
        border-radius: 40px;
    }

    .service-card img {
        width: 120px;
    }

    .service-card h2 {
        font-size: 1.8rem;
    }

    .service-card p {
        font-size: 1.6rem;
    }

    .elemento {
        font-size: 1.2rem;
    }

}